    public static async OnEntry() {
        let req = new proto.http.ReqEntry()
        req.Env = proto.http.ReqEntry.EnumEnv.Dev;   //测试的客户端
        req.GameVersion = "1.0.0"
        req.ResVersion = "1.0.0"
        req.Secret = "s88it786hihfbwsdfgxtxcv_ysdyxv"

        console.log("OnEntry req=", req)
        let respByte = await Http.Post("http://127.0.0.1:8088/entry", proto.http.ReqEntry.encode(req))
        let resp = proto.http.RespEntry.decode(respByte)
        console.log("OnEntry resp=", resp)
    }



